* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  display: flex;
  flex-direction: column;
}

.any-countries__item {
  display: flex;
  flex-direction: center;
  align-items: center;
}

.any-countries__image {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}

.any-countries__title {
  font-size: 15px;
}

.country__name {
  display: flex;
  flex-direction: center;
  align-items: center;
  margin-left: 40px;
}

.country__image {
  width: 70px;
  height: 70px;
  margin-right: 20px;
}

.country-list__item {
  list-style-type: none;
  margin-top: 20px;
  font-size: 20px;
}
